import React, { useState, useEffect } from 'react';
import styles from './Admin3.module.css'; 
import { useNavigate } from 'react-router-dom';
import JugadorService from '../../Services/jugador.service';

const Admin3 = () => {
  const navigate = useNavigate();
  const [mensaje, setMensaje] = useState('');
  const [tuToken, setToken] = useState({ id: 'as' });
  const [jugadores, setJugadores] = useState([
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0},
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0},
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0},
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0},
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0},
    {id_jugador: "",fondo:0, imagen: "req.body.imagen",id_equipo: 1, estado:0}]);
    const [estaConectado, setEstaConectado] = useState([false,false,false,false,false,false]);
    const id_equipo = 5//localStorage.getItem('id_juego')
  const buscarJ =()=>{
    JugadorService.getT(id_equipo).then((res)=>{
      let data= res.data
      let jug=[]
      for (let i in data){
        if(data[i].id_equipo==id_equipo){
          jug.push(data[i])
        }
      }
      setJugadores(jug)
      
    }).catch((res)=>{
      console.log(res)
    })

  }
  
  buscarJ()
  useEffect(() => {
    const timer = setTimeout(() => {
      cambiarEstado();
    }, 5000);
    return () => clearTimeout(timer);
  }, []);
  
  const cambiarEstado = () => {
    let estaConectadoC = estaConectado
    JugadorService.getAll().then((res)=>{
      let data= res.data
      let jug=[]
      for (let i in data){
        if(data[i].id_equipo==id_equipo){
          jug.push(data[i])
        }
      }
      setJugadores(jug)

      for(let j in jugadores){
        console.log(jugadores[j].id_jugador+jugadores[j].estado)
        if( jugadores[j].estado == 0){
          estaConectadoC[j]=false
          
        }
        else{
          estaConectadoC[j]=true
        }
      }
      setEstaConectado(estaConectadoC)
    }).catch((err)=>{
      console.log(err)
    })
    
    

    setEstaConectado((prevState) => !prevState);
  };




  const handleSubmit = (e) => {
    e.preventDefault();
    // Lógica para manejar el envío del formulario
  };

  return (
    <div >
      <div className="row">
        <form className={styles.formAdmin2} onSubmit={handleSubmit}>
          <label className={styles.Titulo}>donaciones del {" equipo "+id_equipo}</label>
          <div className="row">
            {/* Columna ID */}
            <div className="col">
              <h3>ID</h3>
              <div className="row"><p>{jugadores[0].id_jugador}</p></div>
              <div className="row"><p>{jugadores[1].id_jugador}</p></div>
              <div className="row"><p>{jugadores[2].id_jugador}</p></div>
              <div className="row"><p>{jugadores[3].id_jugador}</p></div>
              <div className="row"><p>{jugadores[4].id_jugador}</p></div>  
              <div className="row"><p>{jugadores[5].id_jugador}</p></div>
            </div>
            <div className="col">
              <h3>Fondos</h3>
              <div className="row"><p>{jugadores[0].fondo}</p></div>
              <div className="row"><p>{jugadores[1].fondo}</p></div>
              <div className="row"><p>{jugadores[2].fondo}</p></div>
              <div className="row"><p>{jugadores[3].fondo}</p></div>
              <div className="row"><p>{jugadores[4].fondo}</p></div>  
              <div className="row"><p>{jugadores[5].fondo}</p></div>
            </div>
            <div className="col">
              <h3>Donacion</h3>
              <div className="row"><p>{jugadores[0].id_jugador}</p></div>
              <div className="row"><p>{jugadores[1].id_jugador}</p></div>
              <div className="row"><p>{jugadores[2].id_jugador}</p></div>
              <div className="row"><p>{jugadores[3].id_jugador}</p></div>
              <div className="row"><p>{jugadores[4].id_jugador}</p></div>  
              <div className="row"><p>{jugadores[5].id_jugador}</p></div>
            </div>
            {/* Columna Estado */}
            <div className="col">
              <h3>Estado</h3>
              <div className="row">
              <p className={estaConectado[0] ? styles.conectado : styles.noConectado}>
                 {estaConectado[0] ? 'Conectado' : 'No Conectado'}
              </p>
              </div>
              <div className="row">
              <p className={estaConectado[1] ? styles.conectado : styles.noConectado}>
                  {estaConectado[1] ? 'Conectado' : 'No Conectado'}
              </p>
              </div>
              <div className="row">
              <p className={estaConectado[2] ? styles.conectado : styles.noConectado}>
                  {estaConectado[2] ? 'Conectado' : 'No Conectado'}
              </p>
              </div>
              <div className="row">
              <p className={estaConectado[3] ? styles.conectado : styles.noConectado}>
                  {estaConectado[3] ? 'Conectado' : 'No Conectado'}
              </p>
              </div>
              <div className="row">
              <p className={estaConectado[4] ? styles.conectado : styles.noConectado}>
                  {estaConectado[4] ? 'Conectado' : 'No Conectado'}
              </p>
              </div>
              <div className="row">
              <p className={estaConectado[5] ? styles.conectado : styles.noConectado}>
                  {estaConectado[5] ? 'Conectado' : 'No Conectado'}
              </p>
              </div> 
            </div>
          </div>
            <button type="submit">Comenzar</button>
        </form>
      </div>
    </div>
  );
};

export default Admin3;
